import React, { useState } from 'react';
import AddEmployeePage from '../../view/components/add-employee-page/add-employee-page';
import { Driver_attributes } from '../../types/staff';

const AddEmployeeContainer = () => {
  const [value, setValue] = useState<Driver_attributes>({
    id: 3131313185,
    first_name: '',
    middle_name: '',
    last_name: '',
    sex: 0,
    organization_id: 343438483,
    organization: { id: 343438483, name: 'Any company' },
    personnel_number: '51384312853',
    position: '',
    date_hired: '',
    date_fired: '',
    phone: '',
    driving_license_number: '',
    driving_license_date: '',
    passport: '',
    passport_date: '',
    passport_by: '',
    snils: '',
    face_recognition_id: '',
    license_card_number: '',
    license_card_date_start: '',
    license_card_date_end: '',
    birthday: '',
    min_arterial_pressure: 36,
    max_arterial_pressure: 36,
    min_venous_pressure: 36,
    max_venous_pressure: 36,
    min_pulse: 36,
    max_pulse: 36,
    min_temperature: 36,
    max_temperature: 36,
    unacceptable_alcohol_level: 36,
    chronic_diseases: '',
    diagnosis: '',
    photo: 'https://via.placeholder.com/200',
    med_certificate: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    setValue({ ...value, [e.target.name]: e.target.value });
  };

  const selectChange = (e: React.ChangeEvent<{ name?: string | undefined; value: unknown }>) => {
    e.preventDefault();
    setValue({ ...value, sex: e.target.value === '0' ? 0 : 1 });
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    alert(JSON.stringify(value));
  };

  return (
    <AddEmployeePage
      handleSubmit={handleSubmit}
      handleChange={handleChange}
      selectChange={selectChange}
      value={value}
    />
  );
};

export default AddEmployeeContainer;
